<template>
  <template-view>
    <section class="py-5">
      <div class="container">
        <div class="row">
          <div class="col-md-3" v-for="(poem, index) in poems" :key="index">
            <el-card shadow="hover" v-if="poem.status == 'approved'">
              <a
                href=""
                @click.prevent="
                  $router.push({ name: 'post', params: { id: poem.id } })
                "
                ><img :src="poem.image" class="img-fluid" alt="" style="height:200px;objectFit:cover;width:100%"
              /></a>
              <div>
                <span class="fw-bold">{{ poem.title }}</span>
              </div>
              <div>
                <span style="fontsize: 12px">Writer: {{ poem.writer }}</span>
              </div>
              <div v-if="poem.price == 'Free'">
                <span class="badge bg-danger">{{ poem.price }}</span>
              </div>
              <div v-else>
                <span>Rs.{{ poem.price }}</span>
              </div>
              
            </el-card>
          </div>
        </div>
      </div>
    </section>
  </template-view>
</template>

<script>
import TemplateView from "@/views/TemplateView.vue";
import { mapGetters } from "vuex";
export default {
  components: { TemplateView },
  computed: {
    ...mapGetters({
      poems: "get_poems",
    }),
  },
};
</script>

<style lang="scss" scoped>
</style>