<template>
  <template-view>
    <section class="py-5">
      <div class="container">
        <div class="row">
          <div class="col-md-3" v-for="(result, index) in results" :key="index">
            <el-card shadow="hover" v-if="result.status == 'approved'">
              <a
                href=""
                @click.prevent="
                  $router.push({ name: 'post', params: { id: result.id } })
                "
                ><img :src="result.image" class="img-fluid" alt="" style="height:200px;objectFit:cover;width:100%"
              /></a>
              <div>
                <span class="fw-bold">{{ result.title }}</span>
              </div>
              
              <div>
                <span style="fontsize: 12px">Write: {{ result.writer }}</span>
              </div>
              <div v-if="result.price == 'Free'">
                <span class="badge bg-danger">{{ result.price }}</span>
                 <span class="fw-lighter badge bg-secondary ms-2">{{ result.type }}</span>
              </div>
              <div v-else>
                <span>Rs.{{ result.price }}</span>
                 <span class="fw-lighter badge bg-secondary ms-2">{{ result.type }}</span>
              </div>
            </el-card>
          </div>
        </div>
      </div>
    </section>
  </template-view>
</template>

<script>
import TemplateView from "@/views/TemplateView.vue";
import { mapGetters } from "vuex";
export default {
  components: { TemplateView },
  computed: {
    ...mapGetters({
      results: "get_search",
    }),
  },
};
</script>

