<template>
  <template-view>
    <section class="py-5">
      <div class="container">
        <div class="row">
          <div class="col-md-3" v-for="(story, index) in stories" :key="index">
            <el-card shadow="hover" v-if="story.status == 'approved'">
              <a
                href=""
                @click.prevent="
                  $router.push({ name: 'post', params: { id: story.id } })
                "
                ><img :src="story.image" class="img-fluid" alt="" style="height:200px;objectFit:cover;width:100%"
              /></a>
              <div>
                <span class="fw-bold">{{ story.title }}</span>
              </div>
              <div>
                <span style="fontsize: 12px">Writer: {{ story.writer }}</span>
              </div>
              <div v-if="story.price == 'Free'">
                <span class="badge bg-danger">{{ story.price }}</span>
              </div>
              <div v-else>
                <span>Rs.{{ story.price }}</span>
              </div>
            </el-card>
          </div>
        </div>
      </div>
    </section>
  </template-view>
</template>

<script>
import TemplateView from "@/views/TemplateView.vue";
import { mapGetters } from "vuex";
export default {
  components: { TemplateView },
  computed: {
    ...mapGetters({
      stories: "get_stories",
    }),
  },
};
</script>

